<template>
  <div id="app">
    <div class="header" v-if="!loginPage">
      <img class="logo" src="./assets/logo.png" alt="沃木木艺">
      <div class="right">
        <Menu class="menu" mode="horizontal" :active-name="activeIndex" @on-select="changeMenu">
            <MenuItem name="1">
                <Icon type="ios-paper" />
                分类列表
            </MenuItem>
            <MenuItem name="2">
                <Icon type="ios-people" />
                输入数据
            </MenuItem>
        </Menu>
        <div class="user">
          <Dropdown>
            <Icon type="md-person" /> {{username}}
            <DropdownMenu slot="list">
                <DropdownItem @click.native="logOut">退出登录</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </div>
      </div>
    </div>
    <router-view class="container" :class="{'login-container': loginPage}" />
  </div>
</template>
<script>

export default {
  data() {
    return {
      pathList: ['/', '/input'],
      isManager: common.isManager,
      common: common
    }
  },
  mounted() {
    this.getManager()
  },
  computed: {
    activeIndex() {
      return this.pathList.indexOf(this.$route.path) + 1 +''
    },
    loginPage() {
      return this.$route.path== '/login'
    },
    username() {
      return common.username
    }
  },
  methods: {
    getManager() {
      if(!this.username && !this.loginPage) {
        this.$Message.info("请重新登录!");
        this.$router.push({
          path: '/login'
        })
      }
    },
    changeMenu(value) { 
      this.$router.push({
        path: this.pathList[value-1]
      })
    },
    logOut() {
				localStorage.setItem('username', '')
				localStorage.setItem('isManager', '')
        common.username = ''
        common.isManager = false
        this.$Message.success("退出登录成功");
        this.$router.push({
          path: '/login'
        })
    }
  },
}
</script>
