import Vue from 'vue'
import App from './App.vue'
import router from './router'

import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);
Vue.config.productionTip = false

import axios from 'axios';

window.axios = axios
const pathUrl = 'https://api2.bmob.cn/1/'
const headers = {
  'X-Bmob-Application-Id': 'd5007b04394aef4cdb48560e3a48c6bb',
  'X-Bmob-REST-API-Key': '5bf0663149c84d47c3d5c8949e025815',
  'Content-Type': 'application/json',
}
let common = {
  username: '',
  isManager: false,
  get(url) {
    return new Promise((resolve, reject) => {
      axios({
          method: 'get',
          url: pathUrl + url,
          headers: headers
      }).then(function (res) {
          resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  post(url, data) {
    return new Promise((resolve) => {
      axios({
          method: 'post',
          url: pathUrl + url,
          headers: headers,
          data: data
      }).then(function (res) {
          if(res.status === 200 || res.status === 201) resolve(res.data)
      })
    })
  },
  delete(url, data) {
    return new Promise((resolve) => {
      axios({
          method: 'delete',
          url: pathUrl + url,
          headers: headers,
          data: data
      }).then(function (res) {
        resolve(res.data)
      })
    })
  },
  // 更新数据
  update(url, data) {
    return new Promise((resolve) => {
      axios({
          method: 'put',
          url: pathUrl + url,
          headers: headers,
          data: data
      }).then(function (res) {
        resolve(res.data)
      })
    })
  }
}
router.beforeEach((to, from, next) => {
  // if(to !== '/login') {
  //   const username = localStorage.getItem('username');
  //   const isManager = localStorage.getItem('isManager');
  //   if(username) {
  //     common.username = username
  //   }
  //   console.log(isManager)
  //   if(typeof isManager === "boolean") {
  //     common.isManager = isManager;
  //   }
  // }
  next()
})
const username = localStorage.getItem('username');
const isManager = localStorage.getItem('isManager');
common.headers = headers
if(username) {
  common.username = username
}
if(isManager === 'true') {
  common.isManager = true;
}else if(isManager === 'false') {
  common.isManager = false;
}
window.common = common
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')